.breadcrumb {
  background: none;
}

.breadcrumb {
  font-size: 12px;
  margin: 0em 0em 1em;
  li {
    padding: 0px;
    margin: 0px 0px 0px 7px;
  }
  li a {
    display: inline;
    margin: 0px 0px 0px 0px;
    padding: 0px 7px 0px 0px;
    color: grey;
  }
  li:after {
    font-family: 'Font Awesome 5 Free';
    content: "\f105";
    color: grey;
    font-weight: 900;
    display: inline;
    position: unset;
    right: unset;
    width: auto;
    height: 2em;
    border: none;
    border-width: 0;
    -webkit-transform: translate(0, -50%) rotate(45deg);
    transform: none;
  }
  li span {
    margin: 0px;
    padding: 0px;
  }
}